:root {
  font-family: "inter";
  font-size: 18px;
}

.container-fluid {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

body {
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
.text-gray {
  font-family: "kreon";
  color: #515151;
}

p,
span {
  font-family: "inter";
  padding: 0;
  margin: 0;
  font-family: Inter;
  line-height: 22px;
}

a {
  color: #0F1729;
}

#wrapper {
  background: linear-gradient(90deg, rgb(247, 247, 247) 0%, rgb(247, 247, 247) 50%, rgb(255, 255, 255) 50%, rgb(255, 255, 255) 100%);
}

nav#header {
  background: rgba(0, 0, 0, 0) !important;
}
nav#header .navbar-brand img {
  max-height: 64px;
  height: 100%;
}
@media (max-width: 992px) {
  nav#header .navbar-brand img {
    max-width: 180px;
  }
}
nav#header .navbar-nav {
  justify-content: end;
  column-gap: 45px;
}
nav#header .navbar-nav a.nav-link {
  text-transform: uppercase;
  font-size: 15px;
  font-weight: 500;
  padding-left: 0;
  padding-right: 0;
}
nav#header .languages p,
nav#header .languages span {
  font-family: "kreon";
  font-size: 15px;
  font-weight: 400;
  line-height: 19px;
}

a {
  text-decoration: none;
}

.home_page_section_1 .content {
  padding-top: 200px;
  padding-bottom: 50px;
}
@media (max-width: 1200px) {
  .home_page_section_1 .content {
    padding-top: 100px;
  }
}
@media (max-width: 1200px) {
  .home_page_section_1 .content {
    padding-top: 50px;
  }
}
@media (max-width: 480px) {
  .home_page_section_1 .content {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.offset-negative-lg-1 {
  margin-left: 8.33333333%;
}

.rounded-number {
  display: block;
  width: 30px;
  height: 30px;
  aspect-ratio: 1/1;
  border-radius: 30px;
  line-height: 30px;
  text-align: center;
}

.btn {
  font-size: 16px;
  padding: 15px 30px;
  font-weight: 700;
}
.btn-sm {
  font-size: 14px;
  padding: 10px 20px;
}

.home_page_section_2 ul.splide__pagination {
  justify-content: flex-start;
  padding: 0;
}
.home_page_section_2 ul.splide__pagination button.splide__pagination__page {
  width: 15px;
  height: 15px;
  aspect-ratio: 1/1;
  background-color: #adb5bd;
  opacity: 1;
  border-radius: 15px;
  transition: 0.3s;
}
.home_page_section_2 ul.splide__pagination button.splide__pagination__page.is-active {
  background-color: #5ea828;
  width: 35px;
  height: 15px;
  aspect-ratio: 1/1;
  opacity: 1;
  transform: scale(1);
  border-radius: 15px;
  transition: 0.3s;
}
@media (max-width: 992px) {
  .home_page_section_2 ul.splide__pagination {
    justify-content: center;
  }
}

.card-icon {
  width: 48px;
  height: 48px;
  aspect-ratio: 1/1;
}
.card-icon img {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  aspect-ratio: 1/1;
}
.card-icon-mini {
  width: 40px;
  height: 40px;
}

.partner-button {
  border: 1px solid #DCDCDC;
  font-size: 18px;
  color: #484848;
  transition: 0.3s;
  font-weight: 400;
}
.partner-button:hover {
  background-color: #2b40b4;
  color: #fff;
  transition: 0.3s;
}

.home-page-about-us {
  background-image: linear-gradient(180deg, rgba(215, 215, 215, 0.2), rgba(255, 255, 255, 0)), linear-gradient(180deg, rgb(255, 255, 255), rgb(255, 255, 255));
}

p.motto,
p.experts-exc {
  font-family: "kreon";
  font-size: 32px;
  font-weight: 500;
  line-height: 40.32px;
  position: relative;
}
@media (max-width: 480px) {
  p.motto,
  p.experts-exc {
    font-size: 18px;
    line-height: 30px;
  }
}

p.experts-exc {
  color: #515151;
}

p.motto-sign {
  font-family: "imbue";
  font-size: 222px;
  color: #0F1638;
}
p.motto-sign img {
  width: 100%;
  max-width: 60px;
}
@media (max-width: 480px) {
  p.motto-sign {
    font-size: 100px;
  }
}

.primary-gradient {
  background-image: linear-gradient(90deg, #27367B, #344DBE);
}
@media (max-width: 992px) {
  .primary-gradient-lg {
    background-image: linear-gradient(90deg, #27367B, #344DBE);
  }
}

.after-primary {
  position: relative;
  z-index: 1;
}
@media (min-width: 992px) {
  .after-primary:after {
    background-color: #2b40b4;
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 100vw;
  }
}
@media (max-width: 991px) {
  .after-primary {
    background-color: #2b40b4;
  }
}

.cards-bg .container {
  position: relative;
}
.cards-bg .container:before {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0;
  right: 0;
  height: 5px;
  background-color: #fff;
}
.cards-bg .container:after {
  content: "";
  position: absolute;
  right: -3px;
  top: 0;
  bottom: 0;
  width: 5px;
  background-color: #fff;
}
@media (min-width: 576px) {
  .cards-bg .panel {
    border-bottom: 1px solid #B6B6B6;
    border-right: 1px solid #B6B6B6;
  }
}

.expert-img {
  height: 100px;
}
.expert-img img {
  height: 100px;
  width: 100px;
}

.img-contain {
  object-fit: contain;
  object-position: center;
  max-width: 100%;
  max-height: 100%;
}
.img-cover {
  object-fit: cover;
  object-position: center;
  max-width: 100%;
  max-height: 100%;
}

.services {
  overflow-x: hidden;
}
.services .banner {
  height: 340px;
}
.services .services-motto {
  transform: translateY(-50px);
  position: relative;
}
.services .services-motto:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100vw;
  background-image: linear-gradient(90deg, #27367B, #344DBE);
}
@media (max-width: 992px) {
  .services .services-motto:after {
    transform: translateY(0px);
    width: 100%;
  }
}
.services .services-motto p {
  z-index: 100;
}
@media (max-width: 992px) {
  .services .services-motto {
    transform: translateY(0px);
  }
}

.industries {
  background-color: #fff;
}
.industries .cards {
  position: relative;
  overflow: hidden;
}
.industries .cards::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 5px;
  left: 0;
  right: 0;
  bottom: -3px;
  border-top: 5px solid rgb(255, 255, 255);
  border-left: 5px solid rgb(255, 255, 255);
}
.industries .cards::after {
  content: "";
  position: absolute;
  top: 0;
  right: -3px;
  bottom: 0;
  border-bottom: 5px solid rgb(255, 255, 255);
  border-right: 5px solid rgb(255, 255, 255);
}
.industries .cards .industries-card {
  transition: 0.3s;
}
.industries .cards .industries-card span {
  color: #2b40b4;
  transition: 0.3s;
}
.industries .cards .industries-card:hover {
  background-color: #ced4da;
  color: #fff;
}
.industries .cards .industries-card:hover span {
  color: #fff;
}
@media (min-width: 576px) {
  .industries .cards .industries-card {
    border-bottom: 1px solid #B6B6B6;
    border-right: 1px solid #B6B6B6;
  }
}

footer .footer-logo {
  max-height: 60px;
}
footer .footer-info-lines {
  position: relative;
}
footer .footer-info-lines > * {
  z-index: 2;
}
@media (min-width: 992px) {
  footer .footer-info-lines {
    margin-top: -45px;
  }
  footer .footer-info-lines:after {
    content: "";
    background-image: linear-gradient(90deg, #27367B, #344DBE);
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 50vw;
  }
}
footer .nav-link.active {
  text-decoration: underline;
}
footer .nav-link:hover {
  text-decoration: underline;
}
footer p {
  font-size: 14px;
}

.kreon-font {
  font-family: "kreon";
}

.simple-banner {
  width: 100%;
  height: 342px;
}